html {
  background: var(--tertiary-color);
}

.input {
  display: flex;
  width: 95%;
  position: relative;
  align-items: center;
}

.input_box {
  width: 100%;
  border-radius: 50px;
  padding: 20px 30px;
  font-size: 25px;
  border: none;
  transition: 0.2s;
  box-shadow: inset 0 0 10px black;
}

.input_box:focus {
  box-shadow: 0 0 10px 1000px rgba(0, 0, 0, 0.5);
  outline: none;
}

.input_submit {
  position: absolute;
  width: 100px;
  height: 50px;
  margin: 12px;
  border-radius: 50px;
  right: 0px;
  border: none;
  font-size: 15px;
  background-color: var(--primary-color);
  color: antiquewhite;
  transition: 0.2s all;
  box-shadow: 0 0 5px black;
}

.input_submit:hover {
  background-color: var(--secondary-color);
}

.input_submit:active {
  transform: scale(0.8);
  box-shadow: 0 0 10px black;
}

.container {
  display: flex;
  width: 95%;
  margin-top: 10px;
  justify-content: space-between;
  align-items: flex-start;
}

.quests_heading {
  font-size: 30px;
  color: antiquewhite;
}

.quests {
  display: flex;
  justify-content: space-evenly;
  width: 47.5%;
  flex-direction: column;
  padding: 15px;
  border-radius: 5px;
  background-color: var(--quest-list-color);
}

.single_quest {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: var(--primary-color);
  font-weight: bolder;
  overflow: auto;
  font-family: "Gruppo", serif;
  /* width: 29.5%; */
  /* width: 95.5%; */
  border-radius: 5px;
  padding: 20px;
  margin-top: 15px;
  /* background-image: url("https://img.freepik.com/free-photo/crumpled-yellow-paper-background-close-up_60487-2390.jpg?size=626&ext=jpg"); */
  /* background-image: url("https://unsplash.com/photos/E0AHdsENmDg"); */
  background-color: var(--quest-color);
  transition: 0.2s;
}

.single_quest:hover {
  box-shadow: 0 0 10px black;
  transform: scale(1.025);
}

.single_quest_text {
  flex: 1;
  padding: 5px;
  border: none;
  font-size: 20px;
}

.single_quest_text:focus {
  outline: none;
}

.completed {
  background-color: var(--completed-color);
}

.icon {
  margin-left: 10px;
  font-size: 25px;
  cursor: pointer;
}

.drag {
  box-shadow: 0 0 20px black;
}

.dragactive {
  background-color: var(--quest-drag-color);
}

.dragcomplete {
  background-color: var(--completed-drag-color);
}

@media (max-width: 1400px) {
  .quests {
    width: 45%;
  }
}

@media (max-width: 700px) {
  .input {
    width: 95%;
  }

  .quests {
    width: 95%;
    margin-bottom: 10px;
  }

  .container {
    width: 95%;
    flex-direction: column;
  }
}